header {
  max-width: 767px;
  width: calc(100% - 30px);
  height: 70px;
  margin: 0 auto;
  padding: 0 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: rgba(255, 255, 255, 0.678);

  .header_menu {
    width: 30px;
    height: 30px;
  }

  .logo {
    width: 50px;
    height: 58px;
  }

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.main {
  max-width: 767px;
  width: 100%;
  margin: 0 auto 50px;

  .main_visual {
    width: 100%;
  }

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  .menu {
    display: flex;
    justify-content: center;
    gap: 50px;
    

    figure {
      width: 100px;
      height: 100px;
    }
  }
  .menu figure:hover {
    scale: 1.05;
    transition: all 1s;
  }
}

.tour_list {
  max-width: 767px;
  width: calc(100% - 30px);
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;

  .item {
    width: calc(100% / 2 - 10px);
    height: 100%;
    margin-bottom: 40px;
    position: relative;
    background-color: #FFEDD1;
    border-radius: 10px;
    padding: 10px;

    h4 {
      font-size: 18px;
      margin-bottom: 15px;
      text-align: center;
      word-break: keep-all;
      font-family: 'SokchoBadaBatang';
    }

    figure {
      height: 23.23vw;

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        border-radius: 20px;
      }
    }

    .item--text {
      position: absolute;
      bottom: 0;
      left: 0;
      background-color: #2c2c2cc0;
      padding: 20px 10px;
      width: 100%;
      color: white;
      border-radius: 0 0 20px 20px;
      font-family: 'Pretendard-Regular';
      display: none;
      line-height: 30px;

      p {
        font-weight: bold;
      }
    }
  }

  .item:hover .item--text {
    display: block;
  }
}